  50.999% {
    opacity: 1;
    transform: translate3d(0, 102%, 0);
  }
  51% {
    opacity: 0;
    transform: translate3d(0, 102%, 0);
  }
  51.999% {
    opacity: 0;
    transform: translate3d(0, -102%, 0);
  }
  52% {
    opacity: 1;
    transform: translate3d(0, -102%, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}
